import React from "react"
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import withRoot from '../withRoot';

// material-ui
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Drawer from '@material-ui/core/Drawer';
import { withStyles } from '@material-ui/core/styles';
import { styled } from '@material-ui/styles';

// styles
import { headers, descriptions } from '../styles/typography.ts';
import { buttons } from '../styles/button.ts';


const styles = {
    root: {
      flexGrow: 1,
    },
    drawer: {
        width: 240,
        flexShrink: 0,
    },
    drawerPaper: {
        width: 240,
        paddingTop: 60,
        backgroundColor: '#333',
    },

    h1: headers.h1,
    h2: headers.h2,
    h3: headers.h3,
    h4: headers.h4,
    btnPrimary: buttons.primary,
    btnSecondary: buttons.secondary,
    btnLink: buttons.link,

    navLink: {
        backgroundColor: 'inherit',
        fontFamily: 'Roboto',
        color: '#fff',
        textDecoration: 'none',

        '&:hover':{
            backgroundColor: '#777',          
        },
    },

    sidenav:{
        backgroundColor: 'inherit',
        '&:hover':{
            backgroundColor: '#777',          
        },
    },

    appHeader: {
      padding: 20,
      fontSize: 12,

      '& > h2': {
        fontSize: 22,
        color: '#a8a8a8',
      }
    },
};

const links = [
    {
        label: 'Application Modules',
        link: '/clienteling/modules',
    },
    {
        label: 'Main Menu Items',
        link: '/clienteling/mainMenuItems',
    },

    {
        label: 'App Views',
        link: '/clienteling/appViews',
    },

    {
        label: 'App Variables',
        link: '/clienteling/appVariables',
    },

    {
        label: 'Management Center Configuratrions',
        link: '/clienteling/managementCenterConfigurations',
    },
];

function SideNav(props) {
    const { classes } = props;
    return (
        <Drawer
          className={classes.drawer}
          variant="permanent"
          classes={{
            paper: classes.drawerPaper,
          }}
          anchor="left"
        >
          <div className={classes.appHeader}>
            <Typography className={classes.h1} variant="h2">Clienteling</Typography>  
          </div>
          <List>
            {links.map((item, index) => (
              <ListItem key={index} className={classes.sidenav}>
                {/* <ListItemText primary={item.label} /> */}
                <Link to={item.link} className={classes.navLink}>{item.label}</Link>
              </ListItem>
            ))}
          </List>
        </Drawer>
    );
}

SideNav.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  
export default withStyles(styles)(SideNav);